| **Content-Type** | **是**       | **String**   | **application/json** |

**表 1-2-2** 请求body 内容

| **参数名称**    | **是否必选** | **参数类型** | **说明**                                     |
| --------------- | ------------ | ------------ | -------------------------------------------- |
| **appKey**      | **是**       | **String**   | **代理商公司appKey**                         |
| **companyCode** | **是**       | **String**   | **代理商子公司code**                         |
| **detailList**  | **是**       | **array**    | **报备字段详情(从查询业务报备资料接口获取)** |

**表 1-2-3**  detailList

| **参数名称**     | **是否必选** | **参数类型** | **说明**                                                     |
| ---------------- | ------------ | ------------ | ------------------------------------------------------------ |
| **mainId**       | **是**       | **Integer**  | **组件id**                                                   |
| **propertyKey**  | **是**       | **String**   | **字段key**                                                  |
| **propertyType** | **是**       | **Integer**  | **字段类型 {1:单行文本,2:多行文本框,3:下拉菜单,4:单选按钮,5:多选按钮,6:日期控件,7:数字输入框,8:地区选择,9:附件,10:所属行业,11:单选输入框,12:多选输入框,13:图片}** |
| **tagName**      | **是**       | **String**   | **组件标题**                                                 |
| **value**        | **是**       | **String**   | **组件值, <br />1. 下拉/按钮/单选输入框, value值传code, 多个用逗号分割 <br />2.多选输入框,使用key和value形式json串, 例如\\[{"key":80,"value":"测试1"},{"key":81,"value":"测试"}]<br />3.图片/附件, value值为返回的fileId**<br />**4.行业,value值为行业所属code**<br>**5.话术报备,value值为话术报备新增接口返回的话术id**✔ |

**响应参数**

