| **参数**       | **参数类型** | 说明                                |
| -------------- | ------------ | ----------------------------------- |
| **msg**        | **String**   | **响应描述**                        |
| **statusCode** | **Integer**  | **响应code, code为0是表示响应成功** |

**接口示例**

* **请求示例**

